<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%"
      :row-class-name="tableRowClassName"
    >
      <el-table-column prop="id" label="ID" width="80"> </el-table-column>
      <el-table-column prop="ownerID" label="拥有者ID" width="100">
      </el-table-column>
      <el-table-column prop="img" label="广告图片" width="250">
        <template slot-scope="scope">
          <img :src="scope.row.img" width="30%" alt="" />
        </template>
      </el-table-column>
      <el-table-column prop="theme" label="主题" width="180"> </el-table-column>
      <el-table-column prop="content" label="内容摘要" width="320">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"
            type="primary"
            class="el-icon-circle-check"
            >通过</el-button
          >
          <el-button
            size="mini"
            type="danger"
            class="el-icon-circle-close"
            @click="handleDelete(scope.$index, scope.row)"
            >打回</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-footer style="height: 20px, line-height=20px">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="6"
        layout="prev, pager, next, jumper"
        :total="this.total"
      >
      </el-pagination>
    </el-footer>
  </div>
</template>

<script>
export default {
  name: "Checkad",

  data() {
    return {
      tableData: [],
      currentPage: 1,
      total: 20,
    };
  },

  mounted() {},

  created() {
    this.getData();
  },

  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 1) {
        return "warning-row";
      } else if (rowIndex === 3) {
        return "success-row";
      }
      return "";
    },
    handleEdit(index, row) {
      this.$axios.post("/admin/pass", row).then((res) => {
        this.getData();
      });
    },
    handleDelete(index, row) {
      console.log(row);
      this.$axios.post("/admin/reject", row).then((res) => {
        this.getData();
      });
    },
    getData() {
      this.$axios
        .get("/admin/checkedAD?currentPage=" + this.currentPage)
        .then((res) => {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
          this.currentPage = res.data.data.current;
        });
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getData();
    },
  },
};
</script>

<style lang="less" scoped>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
/deep/.el-table th {
  height: 60px;
  line-height: 60px;
}
/deep/.el-pagination {
  height: 16px;
  line-height: 16px;
}
</style>